import { MDBDataTableV5, MDBBtn } from 'mdbreact';
import React, { Component } from 'react';
import './DataTablesCss.css';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table-next';
import { Link, withRouter } from 'react-router-dom';
import { Button, Col, Row, Form, ListGroup, Card, ListGroupItem } from 'react-bootstrap';


class ExpertisesDataTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ArrayExpertises:[]
        }
    }

  componentWillMount(){
      let tempArr = [];
      for (let i = 0; i < this.props.expertises.length; i++) {
          const expertise = this.props.expertises[i];
          let exper = {
            Code:expertise.Code,
            NameE:expertise.NameE,
            Picture:<img src={expertise.Picture}/>,
            Edit:<div><MDBBtn  type="button"  className="" onClick={()=>this.Edit(expertise)}>Edit</MDBBtn> <MDBBtn  className=""  type="button" onClick={()=>this.Del(expertise)}>Delete</MDBBtn></div>
          }
      
        tempArr.push(exper);
          
      }

      this.setState({
        ArrayExpertises:tempArr
      })
  }

  Del = (e) =>{
console.log(e);
  }

  Edit = (e) => {
      console.log(e)
  }
 
    render() {
        const actions = '<MDBBtn>ffff</MDBBtn>';
        const columns = [
            {
                label: "קוד התמחות",
                field: "Code",
                sort: "asc",
                width: 80,
            },
            {
                label: "שם ההתמחות",
                field: "NameE",
                sort: "asc",
                width: 80,
            },
            {
                label: "תמונה",
                field: "Picture",
                sort: "asc",
                width: 80,
            },
            {
                label: "עריכה",
                field: "Edit",
                sort: "asc",
                width: 80,
            },
           
        ];
        return (
            <div>
            <div className="row">
            <div className="col">
                  <Link to="/"><i className="fa fa-undo" aria-hidden="true"></i></Link>
                </div>
                <div className="row titleDataTable"><h3>עריכת התמחויות</h3></div>
                <div className="row btnAddDataTable"><Button className="BTNCLASS"><h3>הוספת התמחות</h3></Button></div>

            </div>
                <MDBDataTableV5 theadColor="#B5DBF8"
                    paging={true}
                    className="dataTable"
                    sortable
                    striped
                    bordered
                    paginationLabel={["הקודם", "הבא"]}
                    hover
                    entriesOptions={[5, 20, 25]}
                    entries={15}
                    pagesAmount={4}
                    data={{
                        columns: columns,
                        rows: this.state.ArrayExpertises,
                    }} />;
            </div>
        );
    }
}

export default withRouter(ExpertisesDataTable);


