import { MDBDataTableV5, MDBBtn } from 'mdbreact';
import React, { Component } from 'react';
import './DataTablesCss.css';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table-next';
import { Link, withRouter } from 'react-router-dom';
import { Button, Col, Row, Form, ListGroup, Card, ListGroupItem } from 'react-bootstrap';


class HobbiesDataTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ArrayHobbies:[]
        }
    }

  componentWillMount(){
      let tempArr = [];
      for (let i = 0; i < this.props.hobbies.length; i++) {
          const hobby = this.props.hobbies[i];

          let hob = {
            HCode:hobby.HCode,
            HName:hobby.HName,
            Picture:<img src={hobby.Picture}/>,
            Edit:<div><MDBBtn  type="button"  className="" onClick={()=>this.Edit(hobby)}>Edit</MDBBtn> <MDBBtn  className=""  type="button" onClick={()=>this.Del(hobby)}>Delete</MDBBtn></div>
          }
      
        tempArr.push(hob);
          
      }

      this.setState({
        ArrayHobbies:tempArr
      })
  }

  Del = (e) =>{
console.log(e);
  }

  Edit = (e) => {
      console.log(e)
  }
 
    render() {
        const actions = '<MDBBtn>ffff</MDBBtn>';
        const columns = [
            {
                label: "קוד תחביב",
                field: "HCode",
                sort: "asc",
                width: 40,
            },
            {
                label: "שם התחביב",
                field: "HName",
                sort: "asc",
                width: 40,
            },
            {
                label: "תמונה",
                field: "Picture",
                sort: "asc",
                width: 40,
            },
            {
                label: "עריכה",
                field: "Edit",
                sort: "asc",
                width: 60,
            },
           
        ];
        return (
            <div>
             <div className="row">
             <div className="col">
                  <Link to="/"><i className="fa fa-undo" aria-hidden="true"></i></Link>
                </div>
                <div className="row titleDataTable"><h3>עריכת תחביבים</h3></div>
                <div className="row btnAddDataTable"><Button className="BTNCLASS"><h3>הוספת תחביב</h3></Button></div>
            </div>
                <MDBDataTableV5 theadColor="#B5DBF8"
                    paging={true}
                    className="dataTable"
                    sortable
                    striped
                    bordered
                    paginationLabel={["הקודם", "הבא"]}
                    hover
                    entriesOptions={[5, 20, 25]}
                    entries={15}
                    pagesAmount={4}
                    data={{
                        columns: columns,
                        rows: this.state.ArrayHobbies,
                    }} />;
            </div>
        );
    }
}

export default withRouter(HobbiesDataTable);


